<template>    
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('AddGroup')}}</span>                        
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">           
            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit" >
            </dynamic-form> 
            <div class="col-lg-6">               
                <small class="text-danger">{{$t('MandatoryString')}}</small>               
            </div>               
        </div>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
export default{
    data(){
        return{
            isLoading:false,
            Groupname:'',
            ReaderData:[],
            UsersData:[],
            Status:'',
            GroupDetail:'',
            Reader:'',
            Users:'',
            DataLoaded:0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('GroupName'),
                            name: "GroupName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},                            
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Reader'),
                            name: "Reader",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},
                            config: {
                               // option_request_url:process.env.VUE_APP_API_URL+'/GetDeviceList',
                                // token:this.GetUserInfo.Token,
                                options: []
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Users'),
                            name: "Users",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},
                            config: {                               
                                options: []
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{value:"1001",name:'Active'},{value:"1002",name:'InActive'}]
                            },
                        }                        
                    ]
                },
                {
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('GroupDetails'),
                            name: "GroupDescription",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            config: {
                                rows:5
                            }
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
            ],
        }  
    },
     created:async function(){  
       await this.GetDeviceList();
       await this.GetNotficationUsers();
       await this.ManageItemsGroup();
        this.DataLoaded=1;
    },
    methods:{
        GetDeviceList:async function(){          
            await  DataService.GetDeviceList().then(response=>{ 
                response.data.data.forEach((item) => {
                    (this.FormSchema)[0].Data[1].config.options.push({
                        name:`${item.name})`,
                        value:`${item.value}` 
                    })
                }) 
            });
        },
        GetNotficationUsers:async function(){            
            var url=`moduleName=ASSETASSIGNMENT`;
           await DataService.GetNotficationUsers(url).then(response=>{ 
                response.data.data.forEach((item) => {
                    (this.FormSchema)[0].Data[2].config.options.push({
                        name:`${item.NAME}(${item.DEPARTMENT_NAME})`,
                        value:`${item.USER_ID}` 
                    })
                }) 
                console.log((this.FormSchema)[0].Data[2].config.options);               
            });
        },
        onSubmit:function(formfields){  
            var vm=this;
            var jsonobj={
                GroupId:0, 
                GroupName:formfields.GroupName, 
                LocationId:0, 
                ReaderId:formfields.Reader, 
                UserIds:formfields.Users, 
                GroupDetails:formfields.GroupDescription,
                StatusId:formfields.Status
            }                    
            var poststring=JSON.stringify(jsonobj);
            DataService.AddGroup(poststring).then(response=>{                    
            if(response.data=='insert'){
                vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('Group_Item')]), "success", true,  vm.$t("Alert")); 
                vm.onCancel();
            }
            else{
                vm.ShowAlert(vm.$t("Something_Wrong"), "failure", true, vm.$t('Alert'));
            } 
        });  
        },
        onCancel:function(){
            this.$router.push({name:'GroupListing'});
        },
        ManageItemsGroup:async function(){                      
            var url=`id=${this.$route.params.id}`
            await DataService.GetItemsGroupById(url).then(response=>{                                
                if(response.data.length>0){                    
                    (this.FormSchema)[0].Data[0].value=response.data[0].group_name;
                    (this.FormSchema)[0].Data[1].value=response.data[0].reader_id;
                    (this.FormSchema)[0].Data[2].value=response.data[0].user_ids;                   
                    (this.FormSchema)[0].Data[3].value=response.data[0].status_id;
                    (this.FormSchema)[1].Data[0].value=response.data[0].group_details;
                }
            })

        }
    }
}
</script>